import * as React from 'react';
import './App.css';
import { Introduction } from './Introduction'
import { ProjectCarousel } from './ProjectCarousel';
import { Technicals } from './Technicals';
import { Contact } from './Contact';
import { Video, videoData } from './video_data'
import * as Scroll from 'react-scroll'


interface State {
  horizontal: boolean
  index: number
  videos: Video[]
  element: number
  scrollWait: boolean
  transitioningCarousel:boolean
}

export function App() {
  const [state, setState] = React.useState<State>({
    horizontal: true,
    index: 0,
    videos: videoData,
    element: 0,
    scrollWait: false,
    transitioningCarousel:false
  })



  const handleClick = (num: number): void => {

    if(!state.transitioningCarousel){

      const carousel = document.getElementById('carousel')
      carousel?.classList.remove('fade-in')
      carousel?.classList.add('fade-out')
      setTimeout(()=>{
        carousel?.classList.remove('fade-out')
        carousel?.classList.add('fade-in')
      if (num === 1) {
        state.index === state.videos.length - 1 ? setState({ ...state, index: 0 }) : setState({ ...state, index: state.index + 1 })
      } else {
        state.index === 0 ? setState({ ...state, index: state.videos.length - 1 }) : setState({ ...state, index: state.index - 1 })
      }
    },600)
  }
  }

  //Scroll to the element if we should not wait, and do so whenever the state.element is updated. Wait 1200ms after scroll for next scroll

  React.useEffect(()=>{
    if(state.scrollWait){
      Scroll.scroller.scrollTo(String(state.element),{
        duration:800,
        delay:100,
        smooth:true,
      })
      setTimeout(()=>{setState({...state, scrollWait: false})} , 920)
    }

  },[state.scrollWait])

    function preventScroll(e:any){
      
      e.preventDefault()
      e.stopPropagation()

      if(e.deltaY > 0 && !state.scrollWait){
        console.log(e.deltaY)
        if(state.element < 3){
          setState({...state , element:state.element+1 , scrollWait:true})
          }

      }else if(e.deltaY < 0 && !state.scrollWait){
        console.log(e.deltaY)
        if(state.element > 0){
          setState({...state, element:state.element-1 , scrollWait:true})
        }
      }
      return false
    }

    document.body.addEventListener("wheel" , preventScroll , {passive: false} )

  return (
    <>
      <Scroll.Element name="0"><Introduction horizontal={state.horizontal} /></Scroll.Element>
      <Scroll.Element name="1" id="carousel"><ProjectCarousel handleClick={handleClick} video={state.videos[state.index]}  /></Scroll.Element>
      
      <Scroll.Element name="2"><Contact /></Scroll.Element>
    </>
  );
}

