import * as React from 'react'

interface Props {
    horizontal: boolean
}

export function Introduction({ horizontal }: Props) {

    function createWords() {
        const words = "Hi, I'm Bryce"
        let output = []
        for (let letter of words) {
            output.push(
                <h1 className="blue hover-letter-orange" id={letter}>{letter}</h1>
                )
        }
        return <div className="hover-letter-container">{output}</div>
    }

    return (
        <main>
            <div className="central">
                {createWords()}
                <p> <span className="orange" style={{ marginLeft: '4vw' }}>Full stack engineer</span> and <br /> <span className=""style={{ marginLeft: '6vw' }}>creative, critical thinker.</span></p>
                <br />
                <br />
                <p style={{marginLeft:'4vw'}}>See my <span className="orange">projects below</span>,<br /> <span style={{marginLeft:'2vw'}}>scroll further to message!</span></p>
            </div>
            <div className="side">
                <img src="https://photos.smugmug.com/photos/i-NNMRMJm/0/X4/i-NNMRMJm-X4.jpg" alt="" style={{ height: '35vw', width: '28vw', borderRadius: '2vw' }} />
            </div>
            <div>

            </div>
        </main>
    )
}