import * as React from 'react'
import { ContactForm } from './ContactForm'

export function Contact() {

    function createWords() {
        const words = 'Thanks for visiting!'
        let output = []
        for (let letter of words) {
            letter === ' '?
            output.push(
                <h1 className="bg">{letter}</h1>
                )
            :
            output.push(
                <h1 className="blue hover-letter-orange">{letter}</h1>
                )
        }
        return <div className="hover-letter-container">{output}</div>
    }

    return (
        <main style={{ height: '100vh' }}>
            <div className="central" style={{ alignItems: 'center' }}>
                {/* <h1 className="blue">Thanks for visiting!</h1> */}
                {createWords()}
                <p >Want to chat? <span className="orange">Please,</span><br /> <span style={{ marginLeft: "2vw" }}> don't hesitate to <span className="orange">reach out.</span></span></p>
            </div>
            <div className="side">
                <ContactForm />
            </div>
        </main>
    )
}