import * as React from 'react'
import { useForm, ValidationError } from '@formspree/react';

export function ContactForm() {
    const [state, handleSubmit] = useForm("xaykywjk")
    if (state.succeeded) {
        return <p style={{textAlign:'center'}}>Thank you for contacting me! I'll respond as soon as I'm able.</p>;
    }
    return (
        <form onSubmit={handleSubmit}>
            
            <label htmlFor="name" placeholder="Hay Yu">Your name:</label>
            <input type="text" name="name" placeholder="Be creative!" required />

            <label htmlFor="email">Your email:</label>
            <input type="text" name="email" placeholder="This one's important if you want a response." required />
            <ValidationError prefix="Email" field="email" errors={state.errors}/>

            <label htmlFor="subject">Subject:</label>
            <input type="text" name="subject" placeholder="Ex: Let's collaborate! " required />

            <label htmlFor="mesasge">Message:</label>
            <textarea name="message" id="" maxLength={300} cols={30} rows={10} placeholder="Your message here - max length 300 characters."></textarea>
            <ValidationError prefix="Message" field="message" errors={state.errors}/>

            <button type="submit" disabled={state.submitting}>Send message!</button>
        </form>
    )
}
