import * as React from 'react'
import { Video } from './video_data'
import { AiOutlineCaretLeft, AiOutlineCaretRight } from 'react-icons/ai'


interface Props{
    handleClick:(num:number)=>void
    video: Video
}

export function ProjectCarousel({ video, handleClick }:Props) {



    return (
        <main className="carousel">
            <p className="orange link"style={{zIndex:'2' }}><a onClick={()=>{window.open(video.siteUrl , '_blank')}}>{video.title}</a></p>
            <div className="video-viewer">
                <AiOutlineCaretLeft size="10vh" className="icon" onClick={()=>{handleClick(-1)}}/>
                <div className="video-container">
                    <video key={video.url} loop muted autoPlay >
                        <source src={process.env.PUBLIC_URL+`${video.url}`} type="video/mp4" />
                    </video>
                    <div className="caption orange">
                        <br />
                        {/* <h3>{video.description}</h3> */}
                        <h3>{video.technical}</h3>
                    </div>
                </div>
                <AiOutlineCaretRight size="10vh" className="icon" onClick={()=>{handleClick(1)}}/>
            </div>
        </main>
    )
}